import { Directive, ElementRef , Input  , Output, EventEmitter  } from '@angular/core';
import { CdsInternalOverlay } from '@cds/core/internal-components/overlay';

@Directive({ selector: 'cds-internal-overlay' })
export class CdsInternalOverlayDirective {
  protected element: CdsInternalOverlay;

  get cdsMotion() {
    return this.element.cdsMotion;
  }
  @Input() set cdsMotion(value) {
    this.element.cdsMotion = value;
  };
  get closable() {
    return this.element.closable;
  }
  @Input() set closable(value: boolean | '') {
    this.element.closable = value === '' ? true : value;
  };
  get hidden() {
    return this.element.hidden;
  }
  @Input() set hidden(value: boolean | '') {
    this.element.hidden = value === '' ? true : value;
  };
  get i18n() {
    return this.element.i18n;
  }
  @Input() set i18n(value) {
    this.element.i18n = value;
  };
  get closeOverlay() {
    return this.element.closeOverlay;
  }
  @Input() set closeOverlay(value) {
    this.element.closeOverlay = value;
  };

  @Output() cdsMotionChange: EventEmitter<CustomEvent> = new EventEmitter();
  @Output() closeChange: EventEmitter<CustomEvent> = new EventEmitter();

  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
